function timer() {

let deadline = '2019-04-05';

let getTimeRemaining = (endtime) => {
    let t = Date.parse(endtime) - Date.parse(new Date()),
        seconds = Math.floor( (t/1000) % 60 ), // Секунды
        minutes = Math.floor( (t/1000/60) % 60 ), // Минуты
        hours =  Math.floor( (t/(1000*60*60) % 24)); // Часы
        days =  Math.floor( (t/(1000*60*60*24) )); // Дни  

    // Подставил ноль к одиночным цифрам
    if (days <= 9) days = '0' + days;
    if (hours <= 9) hours = '0' + hours;
    if (minutes <= 9) minutes = '0' + minutes;
    if (seconds <= 9) seconds = '0' + seconds;

    return {
        'total' : t,
        'days' : days,
        'hours' : hours,
        'minutes' : minutes,
        'seconds' : seconds
    }
};

let setClock = (id, endtime) => {
   
    let days = document.getElementById('days'), 
        hours = document.getElementById('hours'),
        minutes = document.getElementById('minutes'),
        seconds = document.getElementById('seconds');

    const updateClock = () => {
        let t = getTimeRemaining(endtime);
            days.textContent = t.days;
            hours.textContent = t.hours;
            minutes.textContent = t.minutes;
            seconds.textContent = t.seconds;

        // Остановка таймера и замена значении на нули
        if (t.total <= 0) {
            clearInterval(timeInterval);
            hours.textContent = '00';
            minutes.textContent = '00';
            seconds.textContent = '00';
        }
    }; 
    let timeInterval = setInterval(updateClock, 1000);
    
    return '';
};

setClock('timer', deadline);

}

module.exports = timer;