// require('es6-promise').polyfill();
require('formdata-polyfill');
require('nodelist-foreach-polyfill');
require('formdata-polyfill');

window.addEventListener('DOMContentLoaded', function() {

    'use strict';

    let calcModal = require('./parts/calcModal'),
        decorationTabs = require('./parts/decorationTabs'),
        formModal = require('./parts/formModal'),
        formModalEngineer = require('./parts/formModalEngineer'),
        glazingTabs = require('./parts/glazingTabs'),
        mainForms = require('./parts/mainForms'),
        modalThroughTime = require('./parts/modalThroughTime'),
        photoBig = require('./parts/photoBig'),
        timer = require('./parts/timer');

    calcModal();
    decorationTabs();
    formModal();
    formModalEngineer();
    glazingTabs();
    mainForms();
    modalThroughTime();
    photoBig();
    timer();

});

if ('NodeList' in window && !NodeList.prototype.forEach) {
    console.info('polyfill for IE11');
    NodeList.prototype.forEach = function (callback, thisArg) {
      thisArg = thisArg || window;
      for (var i = 0; i < this.length; i++) {
        callback.call(thisArg, this[i], i, this);
      }
    };
  }