function mainForms() {

// Все формы на сайте

// Сообщения, которые увидит пользователь при отправке формы
let messageForm = {
    loadingForm: 'Загрузка...', // При загрузке
    successForm: 'Спасибо, скоро мы с вами свяжемся! Хорошего дня!', // При Если все хорошо
    failureForm: 'Что-то пошло не так...' // Если ошибка
};

let contactForm = document.getElementsByClassName('forms'), // Все формы на сайте
    formInputText = document.querySelectorAll('input[type=text]'), // все поля с текстом
    formInputTel = document.querySelectorAll('input[type=tel]'), // Все поля с телефоном 
    contactMessage = document.createElement('div'), // Див для сообщении о результате отправки формы
    formData; // Номер формы, сюда передается значение таргета

    // Присваиваю стили для дива с сообщениями
    contactMessage.classList.add('status');

// Проверяю инпут, запрет на буквы, только цифры
formInputTel.forEach( (elem) => {
    // Максимальное число цифр 11 и запрет на буквы при вводе
    elem.setAttribute('maxlength', '11');
    elem.addEventListener('input', function() {
        this.value = this.value.replace(/[^\d(?:+)]/ig, '');
    });
});
// Очищаю все поля после отправки формы
function clearInputForm() {
    // Для полей с именем
    for (let i = 0; i < formInputText.length; i++) {
        formInputText[i].value = '';
    } // Для полей с номером телефона 
    for (let i = 0; i < formInputTel.length; i++) {
        formInputTel[i].value = '';
    }
}

// Отлавливаю форму при клике на любое место страницы
window.addEventListener('submit', function(event) {
    let target = event.target;
    event.preventDefault();
        if (target && target.classList.contains('form')) {
            for (let i = 0; i < contactForm.length; i++) {
                if (target == contactForm[i]) {
                    formData = contactForm[i]; // Начинаю сбор данных из формы
                    formData.appendChild(contactMessage); // Добавляю сообщение 
                    postDataForm() // Функция работы с формой
                        .then( () => contactMessage.innerHTML = messageForm.loadingForm)
                        .then( () => contactMessage.innerHTML = messageForm.successForm)
                        .catch( () => contactMessage.innerHTML = messageForm.failureForm)
                        .then(clearInputForm); // Очищаю все поля ввода
                    break;
            }
        }
    }
});

// Функция работы с формой, запись данных в ajax формате 
function postDataForm() {

    return new Promise(function(resolve, reject) {
        let contactFormData = new FormData(formData);
        let objForm = {}; // Объект где будут передаваться данные пользователя
        contactFormData.forEach(function(value, key) {
            objForm[key] = value; // Сами данные имя - вводимое значение
        }); // Превращию в json 
        let json = JSON.stringify(objForm);
            let request = new XMLHttpRequest();
            request.open('POST', 'server.php'); // Адрес сервера
            request.setRequestHeader('Content-Type', 'application/json; charset=utf-8');  
            request.onreadystatechange = function() {
                if (request.readyState < 4) {
                    resolve();
                } else if (request.readyState === 4) {
                    if (request.status == 200 && request.status < 3) {
                        resolve();
                    }
                    else {
                        reject();
                    }
                }
            };
                request.send(json);
        });
    }

}

module.exports = mainForms;